<template>
    <div>
      <div >
      <div class="shadow search" >
        <div style="font-size: larger">查询</div>
        <el-row :gutter="10" style="margin-top: 10px;margin-bottom:10px">
          <el-col :span="6">
            <el-input
              placeholder="请输入内容"
              v-model="search_input_userId"
              clearable>
            </el-input>
          </el-col>

          <el-col :offset="1" :span="8">
              <el-date-picker
                v-model="value2"
                type="daterange"
                align="right"
                value-format="timestamp"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions">
              </el-date-picker>

          </el-col>
          <el-col :span="8">
            房屋类型:
            <el-select v-model="value" multiple placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <div class="search_btn"  @click="handleSearch">搜索</div>
          </el-col>
        </el-row>
       </div>
      <div class="shadow content">
        <el-row >
          <el-col :span="4" class="inner-title">房屋信息</el-col>
          <el-col :offset="13" :span="4">
            <el-button type="danger" style="margin-top: -5px;margin-left: -23px " @click="handleDeletes">批量删除</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-table
            :data="tableData"
            border
            stripe
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            >
            <el-table-column
              type="selection"
              width="40">
            </el-table-column>
            <el-table-column
              prop="cardId"
              label="cardId"
              align="center"
              width="100">
            </el-table-column>
            <el-table-column
              prop="userId"
              label="userId"
              align="center"
              width="80">
            </el-table-column>
            <el-table-column
              align="center"
              label="type"
              prop="type"
              width="60">
              <template slot-scope="scope">
                <div >{{types[scope.row.type]}}</div>

              </template>
            </el-table-column>

            <el-table-column
              prop="date"
              label="日期"
              align="center"
              width="150">
            </el-table-column>
            <el-table-column
              prop="title"
              label="title"
              align="center"
              width="350">
            </el-table-column>
            <el-table-column
              label="操作"
              width="300"
              align="center"
            >
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="handleView(scope.$index, scope.row)">查看</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block" style="margin-left:20%">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="8"
              layout="prev, pager, next, jumper"
              :total="totalPage">
            </el-pagination>
          </div>
        </el-row>
      </div>
      </div>
      <el-dialog
        title="详情信息"
        :visible.sync="centerDialogVisible"
        width="50%"
        center>
        <div style="margin: 20px;"></div>
        <el-form :label-position="labelPosition" label-width="80px" :model="detail">
          <el-form-item label="帖子类型">
            <div> {{this.detail.type}} </div>
          </el-form-item>
          <el-form-item label="发布人">
            <div> {{this.detail.userId}} </div>
          </el-form-item>
          <el-form-item label="发布时间">
            <div> {{this.detail.postDate}} </div>

          </el-form-item>
          <el-form-item label="title">
            <div> {{this.detail.title}} </div>

          </el-form-item>
          <el-form-item label="房屋图片">
            <div class="block">
              <el-carousel height="150px" style="width: 300px">
                <el-carousel-item v-for="item in detail.images" :key="item">
                  <div   class="small">
                    <img  :src="item">
                  </div>
                </el-carousel-item>
              </el-carousel>
            </div>
          </el-form-item>
          <el-form-item label="介绍">
            <div> {{this.detail.introduce}} </div>

          </el-form-item>

          <el-form-item label="价格">
            <div> {{this.detail.prices[0]}}~{{this.detail.prices[1]}} </div>
          </el-form-item>
          <el-form-item label="面积">
            <div> {{this.detail.squares[0]}}~ {{this.detail.squares[1]}}</div>
          </el-form-item>
          <el-form-item label="户型">
            <div> {{this.detail.unitType}} </div>
          </el-form-item>
          <el-form-item label="地点">
            <div> {{this.detail.location}} </div>

          </el-form-item>
          <el-form-item label="要求">
            <div> {{this.detail.requirement}} </div>

          </el-form-item>

        </el-form>

        <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="centerDialogVisible = false">确定</el-button>
    <el-button type="danger" @click="centerDialogVisible = false">删除</el-button>
   </span>
      </el-dialog>

    </div>
</template>

<script>
  export default {
    name: "Rent",
    data () {
      return {
        centerDialogVisible:false,
        labelPosition: 'right',
        detail:{
          type:'租房',
          userId:'mxf',
          title: "保证界面简洁明确，可操作性强不存在有异议的交互功能",
          introduce: "保证界面简洁明确，可操作性强不存在有异议的交互功能",
          prices: [
            1000, 2000
          ],
          postDate: "2020-08-16",
          squares: [
            100, 150
          ],
          requirement: "保证界面简洁明确，可操作性强不存在有异议的交互功能",
          unitType: 0,
          status: 0,
          unionNum: 2,
          location: "卫津路",
          ownerId: "123",
          images:["https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg",
            "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg",
            'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg']
        },
        search_input_userId:'',
        currentPage: 1,
        totalPage:100,
        options: [{
          value: '选项1',
          label: '出售'
        }, {
          value: '选项2',
          label: '出租'
        }, {
          value: '选项3',
          label: '求购'
        }, {
          value: '选项4',
          label: '求租'
        }
        ],
        types:['','出租','出售','求租','求购','找室友'],
        value: [],

        multipleSelection: [],
        tableData: [
          {
          cardId: "001",
          type: 1,
          userId: "001",
          date: "2020/09/08",
          title: "天津大学"
        },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          },{
            cardId: "001",
            type: 1,
            userId: "001",
            date: "2020/09/08",
            title: "天津大学"
          }, ],
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value2: '',

      }
    },
    methods: {
      changeChoose (data) {
        this.choose = data;
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        // console.log(val)
      },
      handleView(index, row) {


         /**
          * todo 获取帖子详情
          *  / api/adm/getCardDetail/
          */
         let _this = this
         this.$http.get('/ api/adm/getCardDetail/',{
           cardId:_this.tableData[index].cardId
         }).then(res=>{
           console.log(res.data)
           this.detail=res.data

           this.$message.success("查询成功")
         }).catch(err=>{
           console.log(err)
         })
        this.centerDialogVisible = true

      },
      handleDeletes() {
        console.log(this.multipleSelection)

        /**
         * todo 批量删除帖子
         * /api/adm/deleteCard
         */
        let arr = this.multipleSelection
        var cardIds=new Array()
        for ( var i = 0; i <arr.length; i++){
          cardIds[i] = arr[i].cardId
        }
        let _this = this
        this.tableData.splice(index,1)
        this.$http.post('/api/adm/deleteCard',{
          cardIds:cardIds,
        }).then(res=>{
          console.log(res)
          _this.$message.success("删除成功")
        }).catch(err=>{
          console.log(err)
        })
      },

      handleDelete(index, row) {
        /**
         * todo 删除帖子
         * /api/adm/deleteCard
         */
        let _this = this
        console.log(this.tableData[index].cardId)
        this.tableData.splice(index,1)
        this.$http.post('/api/adm/deleteCard',{
          cardIds:[this.tableData[index].cardId],
        }).then(res=>{
          console.log(res)
          _this.$message.success("删除成功")
        }).catch(err=>{
          console.log(err)
        })

      },

      handleDeleteUser(index, row) {
        delete this.tableData[index];
        console.log(index, row);
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        /**
         * todo 查看符合条件的帖子
         * /api/adm/getCard/
         */
        console.log(`当前页: ${val}`);

        var a = new Map()
        a.set(this.options[0].value,0)
        a.set(this.options[1].value,1)
        a.set(this.options[2].value,2)
        a.set(this.options[3].value,3)
        var arr1 = new Array()
        for ( var i = 0; i <this.value.length; i++){
          arr1[i]=a.get(this.value[i])
        }
        let _this = this
        this.$http.get('/api/adm/getCard/',{
          type:arr1,
          userId:_this.search_input_userId===''?null:_this.search_input_userId,
          minDate:this.value2[0]?this.value2[0]:null,
          maxDate:this.value2[1]?this.value2[1]:null,
          page:this.currentPage
        }).then(res=>{
          console.log(res.data)
          console.log(res.totalPage)
          this.tableData=res.data
          this.totalPage=res.totalPage
          _this.$message.success("查询成功")
        }).catch(err=>{
          console.log(err)
        })


      },
      handleSearch(){
        /**
         * 查询符合条件的帖子
         * /api/adm/getCard/
         */

        var a = new Map()
        a.set(this.options[0].value,0)
        a.set(this.options[1].value,1)
        a.set(this.options[2].value,2)
        a.set(this.options[3].value,3)
        var arr1 = new Array()
        for ( var i = 0; i <this.value.length; i++){
          arr1[i]=a.get(this.value[i])
        }
        let _this = this
        this.$http.get('/api/adm/getCard/',{
          type:arr1,
          userId:_this.search_input_userId===''?null:_this.search_input_userId,
          minDate:this.value2[0]?null:this.value2[0],
          maxDate:this.value2[1]?null:this.value2[1],
          page:this.currentPage
        }).then(res=>{
          console.log(res.data)
          console.log(res.totalPage)
          this.tableData=res.data
          this.totalPage=res.totalPage
          _this.$message.success("查询成功")
        }).catch(err=>{
          console.log(err)
        })


      }
    },
    computed : {

    }
  }
</script>

<style scoped>

  .shadow {
    box-shadow: 5px 5px 5px #c0c0c0;
    border-radius: 10px;
  }
  .search{
    margin-left:20px;
    margin-top: 10px;
    margin-right: 15px;
    background-color:#fff;
    height: 95px;
    padding-left: 30px;
    padding-top: 15px;
  }
  .content {
    margin-top: 10px;
    margin-left:20px;
    margin-right: 15px;
    background-color:#fff;
    height: 470px;
    padding-top: 10px;
  }
  .inner-title{
    font-size: larger;
    margin-bottom: 10px;
    margin-left: 30px;
  }

  .search_btn {
    height: 32px;
    line-height:32px;
    cursor: pointer;
    background-color: #f0f0f0;
    border-radius: 6px;
    display: inline-block;
    padding-right: 10px;
    padding-left: 10px;
    margin-left: 10px;
  }

  .small {
  height: 150px;
   width: 300px;
    overflow: hidden;
    position: relative;

  }
  .small img{
    position: absolute;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
</style>



